import React, {Component, Fragment } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import { Route } from 'react-router-dom';
import './Members.css';
import '../../../assets/Common.css'
import { Link } from 'react-router-dom';
import axios from 'axios';

import {
    Table,
    Button,
    Dropdown,
    UncontrolledButtonDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Form,
    FormGroup,
    Input,
    Row,
    Col,
    Pagination,
    PaginationItem,
    PaginationLink
} from 'reactstrap';

import BasicDashboard from '../../Dashboards/Basic/';


import { withRouter } from 'react-router-dom';

const user = 18

class BasicMembers extends Component {
    constructor() {
        super();
        this.state = {
            userData: [],
            currentPage: 1,
            searchKeyword: '',
            itemsPerPage: 10,
            hg: '아이디',
        }; 
    }

    CustomToggle = React.forwardRef(({
        children,
        onClick
    }, ref) => (
        <Button
            ref={ref}
            onClick={(e) => {
                e.preventDefault();
                onClick(e);
            }}
            style={{
                width: '100%',
                textAlign: 'left'
            }}
            variant='light'>
            {children}
        </Button>
    ));
    
    CustomButton = ({label}) => {
        return (
            <Button className={'custom-button'}>
                {label}
            </Button>
        );
    };

    handleDropdownItemSelect = (item) => {
        
        this.setState({
            selectedItem: item
        });
    };

    componentDidMount() {
        this.loadData()
        //setInterval(this.loadData, 5000)
    }

    componentDidUpdate() {

    }


    DetailButton = ({label}) => {
        return (
            <Button
                style={{
                    paddingTop: '3px',
                    paddingBottom: '3px',
                    paddingLeft: '15px',
                    paddingRight: '15px',
                    margin: '0px',
                    width: '100%'
                }}>
                {label}
            </Button>
        );
    };
    
    SearchButton = ({label}) => {
        return (
            <Button style={{
                    width: '100%'
                }}>
                {label}
            </Button>
        );
    };

    async loadData() {
        const { searchKeyword } = this.state; 
        axios
            .get('http://52.78.99.180/api/User/')
            .then(userResponse => {
                const usersWithAdditionalData = [];

                Promise
                    .all(userResponse.data.map(user => {
                        return Promise
                            .all([
                                axios.get(`http://52.78.99.180/api/Hr/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Spo2/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Abnormal_hr/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Stress/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Depress/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Step/${user.User_id}/`)
                            ])
                            .then(([
                                hrResponse,
                                spo2Response,
                                abnormalHrResponse,
                                stressResponse,
                                depressResponse,
                                stepResponse
                            ]) => {
                                var lastHrData = hrResponse.data[hrResponse.data.length - 1];
                                var lastSpo2Data = spo2Response.data[spo2Response.data.length - 1];
                                var lastAbnormalHrData = abnormalHrResponse.data[abnormalHrResponse.data.length - 1];
                                var lastStressData = stressResponse.data[stressResponse.data.length - 1];
                                var lastDepressData = depressResponse.data[depressResponse.data.length - 1];
                                var lastStepData = stepResponse.data[stepResponse.data.length - 1];

                                //to do : null 처리
                                lastHrData = lastHrData
                                    ? lastHrData.Hr
                                    : '미측정';
                                lastSpo2Data = lastSpo2Data
                                    ? lastSpo2Data.Spo2
                                    : '미측정';
                                lastAbnormalHrData = lastAbnormalHrData
                                    ? (lastAbnormalHrData.Abnormal_hr === true)
                                        ? 'Y'
                                        : 'N'
                                    : '미측정';
                                lastStressData = lastStressData
                                    ? lastStressData.Stress
                                    : '미측정';
                                lastDepressData = lastDepressData
                                    ? lastDepressData.Depress
                                    : '미측정';
                                lastStepData = lastStepData
                                    ? lastStepData.Step
                                    : '미측정';

                                if (lastDepressData === 3) 
                                    lastDepressData = "심각";
                                else if (lastDepressData === 2) 
                                    lastDepressData = "중증도";
                                else if (lastDepressData === 1) 
                                    lastDepressData = "경도";
                                else if (lastDepressData === 0) 
                                    lastDepressData = "정상";
                                
                                user.Gps_location = user.Gps_location
                                    ? user.Gps_location
                                    : 'no-idea';

                                const userWithAdditionalData = {
                                    ...user,
                                    hrData: lastHrData,
                                    spo2Data: lastSpo2Data,
                                    abnormalHrData: lastAbnormalHrData,
                                    stressData: lastStressData,
                                    depressData: lastDepressData,
                                    stepData: lastStepData
                                };
                                usersWithAdditionalData.push(userWithAdditionalData);

                                const handleSearch = () => {
                                    // userData 배열에서 검색어를 포함하는 항목만 필터링
                                    const filteredData = userResponse.data.filter(item =>
                                        item.Name.includes(searchKeyword) || item.Hp_num.includes(searchKeyword)
                                    );
                                
                                    // 필터링된 결과를 페이지네이션 처리하여 currentItems에 할당
                                    this.setState({
                                        searchKeyword: 1, // 검색 시 첫 번째 페이지로 초기화
                                        userData: filteredData
                                    });
                                };
                            })
                            .catch(
                                error => console.error(`Error fetching data for user ${user.User_id}`, error)
                            );
                    }))
                    .then(() => {
                        this.setState({
                            userData: usersWithAdditionalData
                        });
                        console.log('userData with additional data: ', usersWithAdditionalData);
                    });
            })
            .catch(userError => console.error('Error fetching userData', userError));
     }

    render() {
        const { userData, currentPage, searchKeyword, itemsPerPage, selectedItem } = this.state; 
        const totalPages = Math.ceil(userData.length / itemsPerPage);
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = userData.slice(indexOfFirstItem, indexOfLastItem);
    
        const paginate = pageNumber => {
            this.setState({
                currentPage: pageNumber
            });
        };

        return (
            <Fragment>
                <TransitionGroup>
                    <CSSTransition
                        component="div"
                        className="TabsAnimation"
                        appear={true}
                        timeout={0}
                        enter={false}
                        exit={false}>

                        <div className='whole-page-ui'>
                            <Row>
                                <Col className='font-size-xxlarge vertical-center'>사용자 현황 관리</Col>
                                <Col className='font-size-small vertical-center horizontal-right'>HOME &gt; 사용자 현황 관리</Col>
                            </Row>
                            <div className='space-10px'></div>
                            <div className='boader-black'></div>
                            <div className='space-20px'></div>
                            <Table className="mb-0" bordered={true}>
                                <tbody>
                                    <tr>
                                        <td className='font-size-large first-column'>검색어</td>
                                        <td>
                                            <UncontrolledButtonDropdown>
                                                <DropdownToggle caret={true} className="mb-2 me-2" color="secondary">
                                                    {selectedItem}
                                                </DropdownToggle>
                                                <DropdownMenu>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('아이디')}>아이디</DropdownItem>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('이름')}>이름</DropdownItem>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('전화번호')}>전화번호</DropdownItem>
                                                </DropdownMenu>
                                            </UncontrolledButtonDropdown>
                                        </td>
                                        <td>
                                            <FormGroup>
                                                <Input type="text" id="keyword" placeholder="검색어를 입력해 주세요"/>
                                            </FormGroup>
                                        </td>
                                        <td>
                                            <this.SearchButton label={'검색'}/>
                                        </td>
                                    </tr>
                                </tbody>
                            </Table>
                            <div className='space-50px'></div>
                            <Table hover={true} className="text-center">
                                <thead>
                                    <tr>
                                        <th>순번</th>
                                        <th>ID</th>
                                        <th>이름</th>
                                        <th>핸드폰</th>
                                        <th>위치</th>
                                        <th>스트레스</th>
                                        <th>우울증</th>
                                        <th>심박이벤트여부</th>
                                        <th>혈중산소포화도</th>
                                        <th>심박수</th>
                                        <th>활동량</th>
                                        <th>관리</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        userData.map((item, index) => (
                                            <tr key={item.User_id}>
                                                <td>{item.User_id}</td>
                                                <td>{item.Id}</td>
                                                <td>{item.Name}</td>
                                                <td>{item.Hp_num}</td>
                                                <td>{item.Gps_location}</td>
                                                <td>{item.stressData}</td>
                                                <td>{item.depressData}</td>
                                                <td>{item.abnormalHrData}</td>
                                                <td>{item.spo2Data}</td>
                                                <td>{item.hrData}</td>
                                                <td>{item.stepData}</td>
                                                <td>
                                                    <Link to={`../../Dashboards/basic/${item.User_id}`}> 
                                                    <this.DetailButton label={'상세보기'}/>
                                                    </Link>
                                                </td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </Table>
                            <Pagination className="justify-content-center mt-3">
                                {
                                    Array
                                        .from({length: totalPages})
                                        .map((_, index) => (
                                            <PaginationItem key={index} active={index + 1 === currentPage}>
                                                <PaginationLink onClick={() => paginate(index + 1)}>
                                                    {index + 1}
                                                </PaginationLink>
                                            </PaginationItem>
                                        ))
                                }
                            </Pagination>
                        </div>
                       
                    </CSSTransition>
                </TransitionGroup>
            </Fragment>
        )
    }

}
export default withRouter(BasicMembers);
